<template>
  <div class="aboutInfo">
     <el-col :span="24" >
				<el-card :body-style="{ padding: '0px' }" style="width:94%; height:70%; margin-left:25px;">
					<img :src="company">
		    </el-card>
    </el-col> 
    <el-col>
      <div class="top">
        <hr><span></span><img :src="cmy" alt="">
      </div>
    </el-col>
    <el-col><div id="brands"><span>our</span> Brands</div></el-col>
    <el-col>
      <div class='inCompany'>
        <div class="inner">
           <div class="gongsi">
             <span class="one"></span><br>
             <span class="two">A</span><br>
             <span class="two"></span>
           </div>
          <img src="../../assets/pic7.png" alt="">
        </div>
      </div>
    </el-col>
    <el-col class="companyInfo">
       <div style="width:680px;" class="aboutContext" v-for='i in info' :key='i'>&nbsp;&nbsp;&nbsp;&nbsp&nbsp;&nbsp;&nbsp;&nbsp{{i}} <br><br></div>
       <img :src="img" alt="" id="rightAbout">
    </el-col>
    <el-col>
      <div id="history"><span>qingcheng</span> history</div>
      <div style="margin-top:50px;">
        <el-steps  align-center status='process'>
          <el-step title="2018 " description=' ' status='process'></el-step>
          <el-step title="2017 " description='  ' status='process'></el-step>
          <el-step title="2016 " description="    " status='process'></el-step>
          <el-step title="2015 " description='2015.01.23 002712)' status='process'></el-step>
          <el-step title="2014 " description=' IPO  ' status='process'></el-step>
        </el-steps>
      </div>
    </el-col>
    <el-col class="children">
      <span></span>
      <div v-for='s in sonCompany' alt="" :key="s">
         <img :src='s' alt="">
      </div>
    </el-col>
  </div>
</template>

<script>
import background from '@/assets/background.jpg'
import cmy from '@/assets/cmy.png'
import context from '@/assets/context.jpg'
import beijing from '@/assets/beijing.jpg'
import shanghai from '@/assets/shanghai.jpg'
import shanghaitwo from '@/assets/shanghaitwo.jpg'
import shanghaithree from '@/assets/shanghaithree.jpg'
import hangzhou from '@/assets/hangzhou.png'
export default {
  name: 'aboutInfo',
  data () {
    return {
			msg: '',
			company:background,
      cmy:cmy,
      info:['4A2014123002712','	2000800','	','90009000','IP++',''],
      img:context,
      sonCompany:[beijing,shanghai,shanghaitwo,shanghaithree,hangzhou]
    }
  },
  created:function(){
    // this.stepHistory();
  },
  methods:{
    next(){
      if (this.active++ > 6) this.active = 1;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .course{
    height: 60px;
    font-size: 20px;
    padding: 10px;
  }
   .top{
     position: relative;
   }
   .top hr{
    border: 4px solid #fff;
    margin-left: 5px;
    display: inline-block;
    width: 100px;
    font-weight: bolder;
    position: absolute;
    top: -595px;
    left: 255px;
  }
  .top span{
    color: #ffffff;
    font-size: 26px;
    font-weight: bolder;
    position: absolute;
    top: -600px;
    left: 130px;
 } 
  .top img{
    position: absolute;
    top: -620px; 
    left: 480px;
  }
  #brands,#history{
      display: inline-block;
    	height: 40px;
			width: 500px;
			line-height: 100px;
			margin-left: 45%; 
			font-family: Arial;
			text-transform:uppercase;
			font-size: 26px;
  }
  #history{
    margin-left: 38%;
  }
  #brands,#history span{
     color: #682F67;
  }
  .inCompany {
     height: 418px;
     width: 1178px; 
     margin-top: 30px;
     margin-left: 26px;
     background: url("../../assets/band.jpg") no-repeat;
     background-color: skyblue;
     border:2px solid #fff;
  }
  .inCompany .inner{
     height: 418px;
     width: 418px;
     background-color:#682F67 ;
     margin-left: 760px;
     text-align: center;
     color: #fff;
     font-size: 28px;
     font-weight: bolder;
  }
  .inCompany .inner span.two{
    font-size: 18px;
    font-weight: lighter;
    color: #fff;
  }
  .inCompany>inner>gongsi>.one{
    margin-top: 40px;
    height: 50px ;
    width: 40px;
    border: 5px solid red;
  }
  .inCompany .inner img{
    margin-top: 80px;
  }
  .companyInfo{
    margin-top: 50px;
  }
   .companyInfo img#rightAbout{
      margin-left: 798px;
      margin-top: -500px ;  
    }
   .companyInfo .aboutContext{
     font-size: 16px;
     color: #50384b;
     margin-left: 20px;
   }
   .el-step__description, .el-step__title,.el-step__head{
     color: #50384b;
   }
   .children span{
     margin-left: 30px;
     font-size: 24px; 
     color: #50384b;
   }
   .children img{
     float: left;
     margin-left: 50px;
     margin-top: 20px;
   }
</style>



